import React from 'react';
import './main.css';
import cards from './../../assets/cards';
import { Link, withRouter } from 'react-router-dom';
import ReactDOM from 'react-dom';
import App from './app';
import {Star, StarLose} from './star';
//import { Router, Route, Switch } from "react-router";
//import { browserHistory } from 'react-router';
//import { history } from 'react-router';

let cardColor = 'card_train';
let el: any;
let rightCard: string;
let deleteCard: number;

function reverseCard() {
  el.classList.remove('flipped');
  el.removeEventListener('mouseleave', reverseCard);
}
function  getEvent (e: any) {
  if (e.target.className === 'card__button') {
    el = e.currentTarget.closest('.card-container');
    el.className += ' flipped';
    el.addEventListener('mouseleave', reverseCard);
  } else playSound(e.currentTarget.dataset.audio);
}
function playSound(addr: string) {
  const audio = new Audio(process.env.PUBLIC_URL + '/' + addr);
  audio.currentTime = 0;
  audio.play();
}
let buttonStartPush = false;
interface IProps {
  index: string,
  train: boolean,
  startGame: () => any,
  gameStarted: boolean,
  // match: this.object.isRequired,
  // location: PropTypes.object.isRequired,
  // history: PropTypes.object.isRequired,
}
interface IState {
  starsList: string[],
  buttonStart: boolean,
  gamePlay:boolean,
  cards:any[],
  playedSoundAddr: string,
}
function repeatSound(addr: string) {
  const buttonStart = document.querySelector('.main__button');
  buttonStart?.classList.add('main__button_push');
  setTimeout(() => {
    buttonStart?.classList.remove('main__button_push');
    }, 500);
  setTimeout(() => {
    playSound(addr)
    }, 600);
}

class Main extends React.Component<IProps, IState> {
  // static propTypes = {
  //   match: PropTypes.object.isRequired,
  //   location: PropTypes.object.isRequired,
  //   history: PropTypes.object.isRequired,
  // };
  constructor(props: IProps) {
    super(props);
    this.state = {
    starsList: [''],
    playedSoundAddr: '',
    buttonStart: false,
    gamePlay: false,
    cards: [...cards[Number(this.props.index)]] as {word: string, translation: string, image: string, audioSrc: string}[],};
  }

  render() {

    const addStar = (el: any) => {
      this.setState((state: any) => ({
        starsList: [...state.starsList, el]
      }))
    }

    const index = this.props.index;
    let train = this.props.train;
    const getCategoryList = () => {
      return (
        cards[0].map((item, index) => {
          const arr = [...cards[index + 1]] as {word: string, translation: string, image: string, audioSrc: string}[];
          const addrImage = '/' + arr[0].image;
          return (
            <div className="card-container" key={arr[0].word}>
              <Link to={"/main/" + (index + 1)} className={'card ' + cardColor} key = {item.toString()}>
                <img src={addrImage} alt={arr[0].word} className="card__image_back"></img>
                {item}
            </Link>
            </div>
          );
        })
      )
    }
    const getGameList = (i: string, train: boolean) => {
      const indexCategory = Number(i);
      return (
        cards[indexCategory].map((item) => {
          const arr = item as {word: string, translation: string, image: string, audioSrc: string};
          const addrImage = '/' + arr.image;
          if (train) {
            return (
              <div className="card-container" key={arr.word}>
                <div className="card card_eng" data-audio={arr.audioSrc} onClick={getEvent}>
                  <img src={process.env.PUBLIC_URL + addrImage} alt={arr.word} className="card__image_front"></img>
                  {arr.word}
                  <div className="card__button"></div>
                </div>
                <div className="card card_ru">
                  <img src={process.env.PUBLIC_URL + addrImage} alt={arr.word} className="card__image_front"></img>
                  {arr.translation}
                </div>
              </div>
            );
          } else {
            return (
              <div className="card-container" key={arr.word}>
                <div className="card" data-audio={arr.audioSrc}>
                  <img src={process.env.PUBLIC_URL + addrImage} alt={arr.word} className="card__image_front" onClick={(e: any) => checkCard(e)}></img>
                </div>
              </div>
            );
          }
        })
      )
    }

    const playGame =() => {
      const cardsCategory = [...this.state.cards];
      console.log('playGame',cardsCategory)
      let randomNumber = Math.floor(Math.random() * cardsCategory.length);
      const addr = cardsCategory[randomNumber].audioSrc;
      console.log(cardsCategory[randomNumber].audioSrc);
      setTimeout(() => playSound(addr), 1000);
      rightCard = cardsCategory[randomNumber].word;
      cardsCategory.splice(randomNumber, 1);
      this.setState((state: any) => ({
        cards: [...cardsCategory],
        playedSoundAddr: addr,
      }))
    }

    const checkCard = (e: any) => {
      if (this.state.gamePlay) {
        const cardsCategory = [...this.state.cards];
        if (e.target.alt === rightCard) {
          playSound('audio/correct.mp3');
          e.target.closest('.card').classList.add('card_guessed');
          addStar(<Star />);
          if (cardsCategory.length > 0) playGame();
          else if(this.state.starsList.length === 8) {
            playSound('audio/success.mp3');
            ReactDOM.render(
              <div className="win-lose-image_wrapper">
                <img src={process.env.PUBLIC_URL + '/img/success.jpg'} alt="success" className="win-lose-image"></img>
              </div>,
              document.getElementById('root')
            );
            setTimeout(() => {
              ReactDOM.render(<App train={true}  />, document.getElementById('root'));
            }, 3000)
          } else {
            playSound('audio/failure.mp3');
            ReactDOM.render(
              <div className="win-lose-image_wrapper">
                <img src={process.env.PUBLIC_URL + '/img/failure.jpg'} alt="failure" className="win-lose-image"></img>
                <span className="win-lose-image__text">You have {this.state.starsList.length - 8} mistake(s)!</span>
              </div>,
              document.getElementById('root')
            );
            //this.props.history.push('/');
            setTimeout(() => {
              ReactDOM.render(<App train={true}  />, document.getElementById('root'));
            }, 3000)
          }
        } else {
          playSound('audio/error.mp3');
          addStar(<StarLose />);
        }
      }
    }
    cardColor = train ? 'card_train' : 'card_game';
    if (train) rightCard = '';
    const gameList = getGameList(index, train);
    const categoryList = getCategoryList();
    const list = index === '0' ? categoryList : gameList;
    let button = '';
    button = (train || index === '0') ? 'main__button hidden' : 'main__button';
    if(this.props.gameStarted && !this.state.gamePlay) {
      this.setState({
        gamePlay: true,
      });
      playGame();
    } else if ((!this.props.gameStarted && this.state.gamePlay)) {

      this.setState({
        gamePlay: false,
        cards: [...cards[Number(this.props.index)]] as {word: string, translation: string, image: string, audioSrc: string}[],
        starsList: [''],
      });
    }

    if (this.state.gamePlay) {
      return (
        <div className="main_wrapper">
          <div className="star-container">
          {this.state.starsList}
          </div>
          <main className="main">
            {list}
          </main>
          <button className="main__button main__button_repeat" onClick={() => repeatSound(this.state.playedSoundAddr)}></button>
        </div>
      )
    } else {
        return (
          <div className="main_wrapper">
            <div className="star-container">
            {this.state.starsList}
            </div>
            <main className="main">
              {list}
            </main>
            <button className={button} onClick={this.props.startGame}></button>
          </div>
        )
      }
  }
}

export default Main;
//export default withRouter(Main);
